<template>
  <div class="song-square">
    <van-sticky>
      <div class="square_header">
        <van-icon name="https://s1.ax1x.com/2020/03/27/GCPaMn.png"
                  size="20"
                  @click="$router.push('/')" />
        <span>歌单广场</span>
      </div>
      <!--  -->
      <div class="square_tab">
        <van-tabs v-model="active"
                  animated
                  color="#dd101b"
                  title-active-color="#dd101b">
          <van-tab :title="item.name"
                   v-for="(item, index) in category"
                   :key="index">
          </van-tab>
        </van-tabs>
      </div>
    </van-sticky>
    <!--  -->
    <div class="square_banner">
      <router-link class="square_item"
                   :to="{name:'collection',params:{id:item.id}}"
                   tag="div"
                   v-for="(item, index) in playList"
                   :key="index">
        <img v-lazy="item.coverImgUrl"
             alt="">
        <div class="square_content">{{item.name}}</div>
      </router-link>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      playList: [],
      active: 0,
      category: []
    }
  },
  methods: {
    // 请求歌单数据
    async getPlaylist () {
      const res = await this.$http.get('top/playlist')
      this.playList = res.data.playlists
    },
    // 获取歌单分类
    async getCategory () {
      const res = await this.$http.get('playlist/catlist')
      this.category = res.data.sub
      // console.log(this.category);
    }
  },
  created () {
    this.getPlaylist()
    this.getCategory()
  },
}
</script>

<style>
.square_header {
  background-color: #fff;
  box-sizing: border-box;
  height: 40px;
  display: flex;
  align-items: center;
  padding: 10px 15px;
}
.square_header span {
  margin-left: 20px;
}
.square_banner {
  display: flex;
  box-sizing: border-box;
  padding: 0 5px;
  width: 100%;
  flex-wrap: wrap;
}
.square_item {
  width: calc(calc(100% / 3) - 10px);
  margin: 10px 5px;
  display: flex;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
  /* border: 1px solid #000; */
}
.square_item img {
  width: 100%;
  height: 115px;
  object-fit: cover;
  border-radius: 5px;
}
.square_content {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  font-size: 13px;
}
</style>


